import {useState, useEffect, useRef} from 'react';
import Spinner from '../spinner/Spinner';
import ErrorMessage from '../errorMessage/ErrorMessage';
import useMarvelService from '../../services/MarvelService';
import { CSSTransition, TransitionGroup } from 'react-transition-group';
import './charList.scss';

const CharList = (props) => {

    const [charList, setCharList] = useState([]);
    const [newItemLoading, setNewItemLoading] = useState(false);
    const [offset, setOffset] = useState(210);
    const [charEned, setcharEned] = useState(false);

    const {loading, error, getAllCharacters} = useMarvelService();


    useEffect(() => {
        onRequest(offset, true);
    }, [])

    useEffect(() => {
        if (charList.length > 0) {
            if (localStorage.getItem('active')) {
                let active = localStorage.getItem('active');
                props.onCharSelected(active);
                focusOnItem(localStorage.getItem('activeId'));
            }
        }
    }, [charList])

    const onRequest = (offset, initial) => {
        initial ? setNewItemLoading(false) : setNewItemLoading(true);
        getAllCharacters(offset)
            .then(onCharListLoaded);
    }

    const onCharListLoaded = (newCharList) => {
        let ended = false;
        if (newCharList.length < 9) {
            ended = true;
        }

        setCharList(charList => [...charList, ...newCharList]);
        setNewItemLoading(newItemLoading => false);
        setOffset(offset => offset + 9);
        setcharEned(charEned => ended);
    }

    const itemRefs = useRef([]);

    const focusOnItem = id => {
        itemRefs.current.forEach(item => item.classList.remove('char__item_selected'));
        itemRefs.current[id].classList.add('char__item_selected');
    }

    function renderItems(arr) {
        const items =  arr.map((item, i) => {
            let imgStyle = {'objectFit' : 'cover'};
            if (item.thumbnail === 'http://i.annihil.us/u/prod/marvel/i/mg/b/40/image_not_available.jpg') {
                imgStyle = {'objectFit' : 'unset'};
            }
            
            return (
                <CSSTransition key={item.id} timeout={500} classNames="char__item">  
                            <li ref={el => itemRefs.current[i] = el}
                                className="char__item"
                                key={item.id}
                                tabIndex={0}
                                onClick={() => {
                                    localStorage.setItem('active', item.id);
                                    localStorage.setItem('activeId', i);
                                    if (localStorage.getItem('active')) {
                                        props.onCharSelected(localStorage.getItem('active'));
                                    } else {
                                        props.onCharSelected(item.id);
                                    }
                                    focusOnItem(i);                       
                                }}
                                onKeyDown={(e) => {
                                    if (e.key === ' ' || e.key === "Enter") {
                                        props.onCharSelected(item.id);
                                        focusOnItem(i);
                                    }
                                }}
                                >
                                    <img src={item.thumbnail} alt={item.name} style={imgStyle}/>
                                    <div className="char__name">{item.name}</div>
                            </li>
                       
                    
                </CSSTransition>
            )
        });
        return (
            <ul className="char__grid">
                <TransitionGroup component={null}>
                    {items}
                </TransitionGroup>
            </ul>
        )
    }
    
    const items = renderItems(charList);

    const errorMessage = error ? <ErrorMessage/> : null;
    const spinner = loading && !newItemLoading ? <Spinner/> : null;

    return (
        <div className="char__list">
            {items}
            {errorMessage}
            {spinner}       
            <button 
            className="button button__main button__long"
            disabled={loading}
            style={{'display': charEned ? 'none' : 'block'}}
            onClick={() => onRequest(offset)}>
                <div className="inner">load more</div>
            </button>
        </div>
    )
}


export default CharList;